<template>
  <v-content class="ma-0 pa-0">
    <v-row>
      <v-col  v-if="!hasDepartment" cols="12" md="4"> <app-panel :loading="loading" title="FTD"   :count="ftd.count" :amount="ftd.amount"></app-panel> </v-col>
      <v-col  v-if="!hasDepartment" cols="12" md="4"> <app-panel :loading="loading" title="Retention"   :count="rtn.count" :amount="rtn.amount"></app-panel> </v-col>
      <v-col  cols="12" :md="hasDepartment ? 12 : 4"> <app-panel :loading="loading"     :count="total.count" :amount="total.amount" :append-icon="isAdmin ? 'mdi-credit-card-outline' : null" @click:icon="showPayments"></app-panel> </v-col>
    </v-row>

    <v-dialog v-model="dialog.value" max-width="600px">
      <v-card >
        <v-card-text>
          <v-data-table :loading="dialog.loading" :items="dialog.items" :headers="dialog.headers">
            <template v-slot:loading   >
              <v-skeleton-loader
                class="mt-6"
                height="94"
                type="list-item-three-line"
              >
              </v-skeleton-loader>
            </template>
            <template v-slot:item.amount="{ item }" >
                {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
            </template>   
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-content>
</template>
<script>
 

// import $ from 'jquery' 
// import dateformat from 'dateformat'
import { mapActions } from 'vuex'
 import AppPanel from './Panel'
export default {
  name: 'WakelTotal',
  components: {
    AppPanel
  },
  props:{
    dates: Array
  },
  data () {
    return {
      dialog: {
        value: false,
        loading: false,
        items: [],
        headers: [
          {
            text: 'Payment Method',
            value: 'payment_method.name'
          },
          {
            text: 'Deposits',
            value: 'count'
          },
          {
            text: 'Total',
            value: 'amount'
          }
        ]
      },
      loading: false,
      total: { count: 0, amount: 0},
      ftd: { count: 0, amount: 0},
      rtn: { count: 0, amount: 0},
     
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotals',
      immediate: true
    }
  },
  computed: {
     isAdmin () {
       const { role } = this.$store.state.auth.user
       return role == 'owner' || role == 'admin'
     },
     hasDepartment () {
       return Boolean(this.$store.state.auth.user.department)
     },
     user () {
       return this.$store.state.auth.user
     }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async showPayments () {
      this.dialog.value = true
      this.dialog.loading = true
      const total = await this.get({ collection: 'dashboard/payment_method', query: {
        type: '!wtd',
        created_on: this.dates.join('|')
      } })
      this.dialog.loading = false
      this.dialog.items = total.data

      console.log(total);
    },
    async fetchTotals () {
      this.loading = true
    
     


      let ftd =    await this.get({ collection: 'department', query: {
        type: 'ftd'
      } })
      ftd = ftd.data.docs[0]._id

      let rtn =    await this.get({ collection: 'department', query: {
        type: 'rtn'
      } })
      rtn = rtn.data.docs[0]._id
       
      const total = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|')
      } })

      if (total.status == 200) {
        const [ allTotal ] = total.data
        this.total.amount = allTotal && allTotal.amount ? allTotal.amount   : 0
        this.total.count = allTotal && allTotal.count ? allTotal.count   : 0
      }


      const ftdTotal = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|'),
        department: ftd
      } })

     
      if (ftdTotal.status == 200  ) {
        const [ ftdallTotal ] = ftdTotal.data
        this.ftd.amount = ftdallTotal && ftdallTotal.amount ? ftdallTotal.amount   : 0
        this.ftd.count = ftdallTotal && ftdallTotal.count ? ftdallTotal.count   : 0
      }
      

      const rtnTotal = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|'),
        department: rtn
      } })
      if (rtnTotal.status == 200 ) {
        const [ rtnallTotal ] = rtnTotal.data
        this.rtn.amount = rtnallTotal && rtnallTotal.amount ? rtnallTotal.amount   : 0
        this.rtn.count = rtnallTotal && rtnallTotal.count ? rtnallTotal.count   : 0
      }

 
      this.loading = false
    }
  } 
 
}
</script>
